// Colors
$primary: #FE3362;
$secondary: #333B4E;
$white: #E5E5E5;
$black: #26323F;
// $muted:#B6C0CB

// FX
$shadow: 0 5px 30px 0px rgba(88, 113, 148, .08);
$easing: cubic-bezier(.885, 0, .335, .99);

// Responsive breakpoints
$small: 576px;
$medium: 768px;
$large: 992px;
$xlarge: 1367px;

// Misc.
$font: 'Montserrat', sans-serif;
$border-radius: 1.071rem;
$border-radius-sm: $border-radius / 2;
